import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/pettyCash/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle,
  StatusBadge,
  TransNo
} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

class PettyCash extends Component {
  state = {
    filter: {
      id_location: null,
      id_petty_cash_type: null,
      id_employee: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getPettyCashes } = this.props;
    getPettyCashes();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCash.error) {
      notification("error", this.props.pettyCash.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.pettyCash.success) {
      notification("success", this.props.pettyCash.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getPettyCashes } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_petty_cash_type: null,
        id_employee: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    getPettyCashes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPettyCashes } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_petty_cash_type: null,
        id_employee: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getPettyCashes();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getPettyCashes } = this.props;
    //const { current_page } = this.props.pettyCash;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_petty_cash_type) {
      string
        ? (string += "&id_petty_cash_type=" + filter.id_petty_cash_type)
        : (string += "id_petty_cash_type=" + filter.id_petty_cash_type);
    }
    if (filter.id_employee) {
      string
        ? (string += "&id_employee=" + filter.id_employee)
        : (string += "id_employee=" + filter.id_employee);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(this.state.filter)
    // console.log(string)
    this.setState({ search: null });
    getPettyCashes(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPettyCashes } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_petty_cash_type: null,
        id_employee: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getPettyCashes();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePettyCash(id);
    } else {
      this.props.deactivatePettyCash(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getPettyCashes } = this.props;
    const { search, filter } = this.props.pettyCash;
    getPettyCashes(page, search, filter);
  };

  

  listAction = {
    title: "",
    dataIndex: "id_petty_cash",
    rowKey: "id_petty_cash",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  render() {
    
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({id:"pettyCashes.no"}),
        dataIndex: "petty_cash_no",
        rowKey: "petty_cash_no",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            // case 2:
            //   className = "closed";
            //   break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
        }
      },
      {
        title: getLanguage({id:"date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({id:"sidebar.location"}),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id:"sidebar.pettyCashType"}),
        dataIndex: "petty_cash_type.description",
        rowKey: "petty_cash_type.description",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id:"amount"}),
        dataIndex: "amount",
        rowKey: "amount",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id:"relatedEmployee"}),
        dataIndex: "employee",
        rowKey: "employee",
        width: "20%",
        render: text => {
          if (text !== null && text.last_name !== null) {
            return <span> {text.first_name + " " + text.last_name} </span>;
          } else if (text !== null && text.last_name === null) {
            return <span> {text.first_name} </span>;
          } else {
            return <span />;
          }
        }
      },
      // {
      //   title: "Related Employee",
      //   dataIndex: "employee_name",
      //   rowKey: "employee_name",
      //   width: "20%",
      //   render: text => {
      //     if (text && text.length > 25) {
      //       return (
      //         <Tooltip placement="bottom" title={text}>
      //           {text.substring(0, 25) + "..."}
      //         </Tooltip>
      //       );
      //     } else {
      //       return text;
      //     }
      //   }
      // },
      {
        title: getLanguage({id:"createdBy"}),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id:"status"}),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({id:"open"});
              break;
            case 2:
              className = "ordered";
              display = getLanguage({id:"processed"});
              break;
            // case 2:
            //   className = "closed";
            //   display = "Processed";
            //   break;
            case 3:
              className = "cancelled";
              display = getLanguage({id:"cancelled"});
              break;
            default:
              className = "new";
              display = getLanguage({id:"open"});
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const {
      petty_cashes,
      locations,
      petty_cash_types,
      employees
    } = this.props.pettyCash;
    //console.log(this.props.pettyCash); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const { loading, loadingForeign } = this.props.pettyCash;

    //console.log(permissions);
    if (
      permissions.includes("Update Petty Cash") &&
      columns.length === 8
    ) {
      columns.push(this.listAction);
    }

    Object.keys(petty_cashes).map((petty_cash, index) => {
      return dataSource.push({
        ...petty_cashes[petty_cash],
        key: petty_cash
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.pettyCash;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="pettyCashes.pageHeader" />}
            marginBottom={true}
          >
            {permissions.includes("Create Petty Cash") ? (
              <Link to={`${this.props.match.path}/add`}>
                <Button
                  // loading={loading}
                  // disabled={loading}
                  type="primary"
                  className="mateAddInvoiceBtn"
                >
                  <IntlMessages id="pettyCashes.button.add" />
                </Button>
              </Link>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({id:"pettyCashes.no"}) + ", " +
                getLanguage({id:"createdBy"})
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>

          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="pettyCashes.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="pettyCashTypes.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_petty_cash_type"
                      )}
                      value={
                        this.state.filter.id_petty_cash_type
                          ? this.state.filter.id_petty_cash_type
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="pettyCashes.filter.select.pettyCashType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {petty_cash_types.map((data, key) => (
                        <Option key={key} value={data.id_petty_cash_type}>
                          {data.petty_cash_type_code +
                            " (" +
                            data.description +
                            ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="pettyCashes.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                          this.state.filter.end_date
                          ? [
                            moment(this.state.filter.start_date),
                            moment(this.state.filter.end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="relatedEmployee" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_employee"
                      )}
                      value={
                        this.state.filter.id_employee
                          ? this.state.filter.id_employee
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="pettyCashes.filter.select.employee" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {employees.map((data, key) => (
                        <Option key={key} value={data.id_employee}>
                          {data.employee_code +
                            " (" +
                            data.first_name +
                            (data.last_name !== null
                              ? " " + data.last_name + ")"
                              : ")")}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="space-between" gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                        //showArrow={false}
                        >
                          <Option value={1}>{getLanguage({id:"open"})}</Option>
                          <Option value={2}>{getLanguage({id:"processed"})}</Option>
                          <Option value={3}>{getLanguage({id:"cancelled"})}</Option>
                        </Select>
                      </Col>
                      <Col span={8}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.submit" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {petty_cashes.length === 0 && !this.props.pettyCash.loading ? (
              <Empty
                description={<IntlMessages id="pettyCashes.table.noData" />}
              />
            ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      // size="small"
                      dataSource={dataSource}
                      columns={columns}
                      loading={loading}
                      className="invoiceListTable"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={
                        page_size
                          ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                          : true
                      }
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    pettyCash: state.pettyCash,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(PettyCash);
